import React,{useState, useEffect} from 'react';
import style from './Navbar.module.css';
import sml_logo from './sml_logo.png';
function Navbar({menuof, setmenuof, opened,login}){
    let today = new Date();
    const [currentTime, setcurrentTime] = useState(today.toLocaleTimeString());
    useEffect(()=>{
        let timer = window.setInterval(()=>{
            console.log(today.toLocaleTimeString());
            setcurrentTime(today.toLocaleTimeString());
        },1000);
        return ()=>window.clearInterval(timer);
    });
    return(
        <div className={`${style.wrapper}`}>
            <div className={`${style.icon}`} onClick={()=>setmenuof(!menuof)}>
                <img src={sml_logo}/>
            </div>
            {opened}
            <div className={`${style.time}`}>{currentTime}</div>
        </div>
    )
}
export default Navbar;