import React, {useState, useEffect} from 'react';
import './App.css';
import Navbar from './Components/Navbar.js'
import Menu from './Components/Menu'
import Window from "./Components/Window";
import Startup from './Startup.js';
import jQuery from 'jquery';
import icon_intro from './intro.svg';
import icon_members from './members.svg';
import icon_study from './study.svg';
import icon_cal from './cal.svg';
import icon_gallery from './gallery.svg';

let $ = window.jQuery = jQuery;
function App() {
    const [startup, setstartup] = useState(true);
    const [login, setlogin] = useState('undefined');

    const [menuof, setmenuof] = useState(false);
    const [id,setid]=useState(0);
    const [opened, setopened] = useState([]);
    const [current, setcurrent] = useState(-1);
    const [indexupdater, setindexupdater] = useState(1);
    const [nowdelete,setnowdelete] = useState(-1);
    useEffect(()=>{
        setindexupdater(indexupdater+1);
        $(function(){
            $(`#window${current}`).css("z-index",`${100*indexupdater}`);
        });
    },[current]);
    useEffect(()=>{
        setopened(opened.filter(x=> x.key != nowdelete));
    },[nowdelete]);

    return (
        <>
            {startup && <Startup setstartup={setstartup} login={login} setlogin={setlogin}/>}
            <div className="Home-wrapper">
                <Navbar menuof = {menuof} setmenuof = {setmenuof} opened = {opened} current ={current} setcurrent ={setcurrent} indexupdater={indexupdater} login={login}/>
                {menuof && <Menu  setopened={setopened} opened = {opened} setid={setid} id={id} login={login}/>}

                <div className={`icon`} style={{top: `100px`, left: `60px`}} onClick={()=>{
                    setopened([...opened,<Window title={`Intro`} key={id} keyf={id} current={current} setcurrent={setcurrent} setnowdelete={setnowdelete}/> ]);
                    setid(id+1);
                }}>
                    <img src={icon_intro}/>
                    <span>INTRO</span>
                </div>

                <div className={`icon`} style={{top: `250px`, left: `60px`}} onClick={()=>{
                    setopened([...opened,<Window title={`Members`} key={id} keyf={id} current={current} setcurrent={setcurrent} setnowdelete={setnowdelete}/> ]);
                    setid(id+1);
                }}>
                    <img src={icon_members}/>
                    <span>MEMBERS</span>
                </div>

                <div className={`icon`} style={{top: `400px`, left: `60px`}} onClick={()=>{
                    setopened([...opened,<Window title={`Study`} key={id} keyf={id} current={current} setcurrent={setcurrent} setnowdelete={setnowdelete}/> ]);
                    setid(id+1);
                }}>
                    <img src={icon_study}/>
                    <span>STUDY</span>
                </div>

                <div className={`icon`} style={{top: `550px`, left: `60px`}} onClick={()=>{
                    setopened([...opened,<Window title={`Calendar`} key={id} keyf={id} current={current} setcurrent={setcurrent} setnowdelete={setnowdelete}/> ]);
                    setid(id+1);
                }}>
                    <img src={icon_cal}/>
                    <span>CALENDAR</span>
                </div>

                <div className={`icon`} style={{top: `700px`, left: `60px`}} onClick={()=>{
                    setopened([...opened,<Window title={`Gallery`} key={id} keyf={id} current={current} setcurrent={setcurrent} setnowdelete={setnowdelete}/> ]);
                    setid(id+1);
                }}>
                    <img src={icon_gallery}/>
                    <span>GALLERY</span>
                </div>
            </div>
        </>

    );
}

export default App;
