import React, {useState, useEffect} from 'react';
import style from './Window.module.css';
import Window_content from './Window_content.js'
import jQuery from 'jquery';
let $ = window.jQuery = jQuery;
function Window({title, keyf, current, setcurrent, indexupdater, setopened, opened, deletewindow, setnowdelete}) {
    const [onoff, setonoff] = useState(false);
    useEffect(()=>{
        setcurrent(keyf);
    },[]);
    return(
        <div>
            <div className={`${style.content}`} onClick={()=>{setonoff(!onoff); setcurrent(keyf); console.log(`keyf:${keyf}`)}}> {title} </div>
            <Window_content title={title} keyf = {keyf} indexupdater={indexupdater} setcurrent={setcurrent} setopened={setopened} opened={opened} deletewindow={deletewindow} setnowdelete={setnowdelete}/>
        </div>
    )
}
export default Window;