import React, {useState, useEffect} from 'react';
import Style from './Startup.module.css';
import logo_big from './logo_big.svg'
import jQuery from 'jquery';
let $ = window.jQuery = jQuery;
function Startup({setstartup, login,setlogin}) {
    let opening = () => {
        return new Promise((resolve, reject)=>{
            $(function(){
                let height = window.innerHeight;
                console.log(height);
                $(`#Startup`).css(`top`,`-${height+100}px`);
                $(`#Startup`).css(`opacity`,`0`);
            });
            setTimeout(()=>{resolve();},5000);
        })

    };
    let inputvalue = (e) =>{
        setlogin(e.target.value);
    };
    return (

       <div className={Style.Wrapper} id={`Startup`}>
           <div className={Style.InnerWrapper}>
               <img src={logo_big}/>
               <br/>
               <input onChange={inputvalue} className={Style.username} id={`Username`} type={`text`} placeholder={`Username`} />
               <br/>
               <button className={Style.container} value={`Connect`} onClick={()=>{
                   opening().then(()=>{setstartup(false)});
                   }}>Connect</button>
           </div>
       </div>
    )
}

export default Startup;