import React, {useState, useEffect} from 'react';
import style from './Window_content.module.css';
import jQuery from 'jquery';
import close from './close.png';
import minimize from './minimize.png';

let $ = window.jQuery = jQuery;
function Window_content({title, keyf, indexupdater, setcurrent, setnowdelete}) {
    const windowStyle = {
        top: `${100+10*keyf}px`,
        left: `${100+10*keyf}px`,
        border : '1px solid black',
        width: '500px',
        height: '500px',
        zIndex: `${keyf}`
    }
    useEffect(()=>{
        const el = document.querySelector(`#window${keyf}`);
        el.addEventListener('mousedown',mousedown,false);
        function mousedown(e){
            window.addEventListener('mousemove',mousemove,true);
            window.addEventListener('mouseup',mouseup,true);
            console.log(`mousedown:${e.clientX}`);
            console.log(`mousedown:${e.clientY}`);
            let prevX = e.clientX;
            let prevY = e.clientY;
            function mousemove(e){
                el.style.top = parseInt(el.style.top)+ e.clientY - prevY + 'px';
                el.style.left = parseInt(el.style.left)+ e.clientX - prevX + 'px';
                prevX = e.clientX;
                prevY = e.clientY;
            }
            function mouseup(e){
                window.removeEventListener('mousemove',mousemove,true);
                window.removeEventListener('mouseup',mouseup,true);
            }
        }

    },[]);

    const [inindexupdater,setindexupdater] = useState(indexupdater);
    useEffect(()=>{setindexupdater(indexupdater)},[indexupdater])
    return(
        <div className={style.Wrapper} id={`window${keyf}`} style={windowStyle} key={indexupdater} onClick={()=>{setcurrent(keyf)}}>
            <div className={style.topbar} id={`topbar${keyf}`}>
                <span style={{paddingLeft: `0.8rem`}}>{title}</span>
                <div className={style.iconWrapper}>
                    <img src={minimize} />
                    <img src={close} onClick={()=>{setnowdelete(keyf)}}/>
                </div>
            </div>
            <div className={style.Content}></div>
        </div>
    )
}
export default Window_content;